<template>
  <div>
    <button @click="input('1')">1</button>
    <button @click="input('2')">2</button>
    <button @click="input('3')">3</button>
    <button @click="input('4')">4</button>
    <button @click="input('5')">5</button>
    <button @click="input('6')">6</button>
    <button @click="input('7')">7</button>
    <button @click="input('8')">8</button>
    <button @click="input('9')">9</button>

    <button @click="input('0')">0</button>
    <button @click="input('.')">.<sub>decimal</sub></button>
  </div>
</template>

<script>
export default {

  props: {
    locus: {
      type: String,
      required: true,
    },

    service: {
      type: String,
      default: ['state', 'nebula'][0]
    }
  },

  methods: {
    input: function (val) {
      if(this.service == 'nebula'){
        this.$nebula(this.locus, val);
        return;
      }

      let str = this.$read(this.locus);

      //not leading 0
      if (val == '0' && (str == '')) {
        this.log('leading zero detected');
        return;
      }

      //not extra decimal point
      if (val == '.') {
        if (str == '') {
          str = '0';
        }
        if (str.includes('.')) {
          return;
        }
      }

      str = str.concat(val);
      this.$write(this.locus, str);
    }
  },

  computed:{
    events: function () {
      return this.$read('events');
    },
  },

  watch: {
    events: function (fresh, stale) {
      if(this.service == 'nebula'){
        return;
      }

      switch (fresh) {
        case 'recorded.miss':
        case 'recorded.hit':
        case 'game.lost':
        case 'game.new':
          this.$write('numberPad', '');
          break;
      }
    }
  },

  mounted: function () {
    if (this.service != 'nebula') {
      var input = this.input;
      this.$onNebula(this.locus, (value) => {
        if(_.isEmpty(value)){
          return;
        }
        this.input(value);
        this.$nebula(this.locus, '');
      });
    }
  }

}
</script>

