<template>

  <!--{{ tagSpecs }}-->
  <component :is="tagName(tagSpec(tagSpecs, 0))"
             :class="tagClasses(tagSpec(tagSpecs, 0))"
             v-if="isHtml"
             v-show="!_.isEmpty(tryContent)"
             v-html=this.htmlDynamicFilters(tryContent)>
  </component>

  <component :is="tagName(tagSpec(tagSpecs, 1))"
             :class="tagClasses(tagSpec(tagSpecs, 1))"
             v-else
             v-show="!_.isEmpty(tryContent) || _.isNumber(tryContent)"
  >{{ tryContent | dynamicFilters(filters) }}
  </component>

</template>

<script>
import {mapGetters} from 'vuex'
import _Services from "./_Services";

export default {
  extends: _Services,

  props: {
    default: {
      type: String,
      default: ''
    },

    tagSpecs: {
      type: String,
      default: "div,span"
    },

    isHtml: {
      type: Boolean,
      default: false
    },

    filters: {
      type: Array,
      default: function (){
        return [];
      }
    }
  },

  methods:{
    htmlDynamicFilters: function (){

      return this.$options.filters
          .dynamicFilters(this.tryContent, this.filters);
    }
  },

  computed: {

    tryContent: function (){
      let val = this.serviceContent || this.default;

      return String(val);
    }
  }

}
</script>